import _ from "lodash";

export default class MachineType {
  //
  static get POSITION_HEAD() {
    return "";
  }
  //
  static get POSITION_BODY() {
    return "";
  }
  //
  static get POSITION_RIGHTARM() {
    return "";
  }
  //
  static get POSITION_LEFTARM() {
    return "";
  }
  //
  static get POSITION_RIGHTLEG() {
    return "";
  }
  //
  static get POSITION_LEFTLEG() {
    return "";
  }

  static getPositionConst() {
    return {
      POSITION_HEAD: MachineType.POSITION_HEAD,
      POSITION_BODY: MachineType.POSITION_BODY,
      POSITION_RIGHTARM: MachineType.POSITION_RIGHTARM,
      POSITION_LEFTARM: MachineType.POSITION_LEFTARM,
      POSITION_RIGHTLEG: MachineType.POSITION_RIGHTLEG,
      POSITION_LEFTLEG: MachineType.POSITION_LEFTLEG
    };
  }

  constructor(
    weight,
    size,
    hasDoubleSeat,
    baseMovility,
    baseEvadeRate,
    initiative,
    headSlot,
    bodySlot,
    leftArmSlot,
    rightArmSlot,
    leftLegSlot,
    rightLegSlot
  ) {
    // // 
    // this.name = name
    // 
    this.weight = weight;
    // 
    this.size = size;
    // 
    this.hasDoubleSeat = hasDoubleSeat;
    // 
    this.baseMovility = baseMovility;
    // 
    this.baseEvadeRate = baseEvadeRate;
    // 
    this.initiative = initiative;

    this.slots = {};
    // // 
    this.slots[MachineType.POSITION_HEAD] = headSlot;
    // 
    this.slots[MachineType.POSITION_BODY] = bodySlot;
    // 
    this.slots[MachineType.POSITION_RIGHTARM] = leftArmSlot;
    // 
    this.slots[MachineType.POSITION_LEFTARM] = rightArmSlot;
    // 
    this.slots[MachineType.POSITION_RIGHTLEG] = leftLegSlot;
    // 
    this.slots[MachineType.POSITION_LEFTLEG] = rightLegSlot;
  }

  getSlot(position,slotCorrection) {
    let retVal = 0
    if (
      position === MachineType.POSITION_HEAD ||
      position === MachineType.POSITION_BODY
    ) {
      retVal = this.hasDoubleSeat
        ? this.slots[position] + 1
        : this.slots[position];
    } else {
      retVal = this.slots[position];
    }
    if(slotCorrection) retVal += slotCorrection
    return retVal;
  }

  //** */
  getTotalSlot(totalSlotCorrection) {
    let ret = 0;
    for (let slot in this.slots) {
      ret += this.getSlot(slot);
    }
    if(totalSlotCorrection) ret += totalSlotCorrection
    return ret;
  }

  get headSlot() {
    return this.getSlot(MachineType.POSITION_HEAD);
  }

  get bodySlot() {
    return this.getSlot(MachineType.POSITION_BODY);
  }

  get rightArmSlot() {
    return this.getSlot(MachineType.POSITION_RIGHTARM);
  }

  get leftArmSlot() {
    return this.getSlot(MachineType.POSITION_LEFTARM);
  }

  get rightLegSlot() {
    return this.getSlot(MachineType.POSITION_RIGHTLEG);
  }

  get leftLegSlot() {
    return this.getSlot(MachineType.POSITION_LEFTLEG);
  }

  get evadeRate() {
    if (this.hasDoubleSeat) {
      return this.weight === ""
        ? this.baseEvadeRate + 1
        : this.baseEvadeRate + 2;
    } else {
      return this.baseEvadeRate;
    }
  }

  get movility() {
    if (this.hasDoubleSeat) {
      let retVal =
        this.weight === "" ? this.baseMovility - 2 : this.baseMovility - 1;
      return Math.max(retVal, 2);
    } else {
      return this.baseMovility;
    }
  }

  get armorPoint() {
    return this.getArmorPoint()
  }

  get constitution() {
    return this.getConstitution()
  }
  get chargeDamage() {
    return this.getChargeDamage()
  }
  get coveredChargeDamage() {
    return this.getCoveredChargeDamage()
  }
    
  //ArmorPoint
  getArmorPoint(armorPointCorrection) {
    let retVal = 0;
    if (this.weight === "") {
      retVal = this.hasDoubleSeat ? 2 : 3;
    } else if (this.weight === "") {
      retVal = this.hasDoubleSeat ? 5 : 6;
    }

    if (armorPointCorrection) retVal += armorPointCorrection;
    return retVal;
  }

  //ArmorPoint
  getConstitution(armorPointCorrection,totalSlotCorrection) {
    let retVal =
      this.movility +
      this.getTotalSlot(totalSlotCorrection) +
      this.getArmorPoint(armorPointCorrection) +
      1; //
    //
    if (this.size === "LL") {
      retVal += 2;
    } else {
      retVal += 1;
    }
    return retVal;
  }

  //
  //ArmorPoint
  getChargeDamage(armorPointCorrection) {
    let constitution = this.getConstitution(armorPointCorrection);
    if (constitution === undefined || constitution === 0) {
      return "";
    } else {
      return Math.ceil(constitution / 4);
    }
  }

  //
  //ArmorPoint
  getCoveredChargeDamage(armorPointCorrection) {
    let constitution = this.getConstitution(armorPointCorrection);
    if (constitution === undefined || constitution === 0) {
      return "";
    } else {
      return Math.ceil(constitution / 10);
    }
  }

  //
  get chargeCount() {
    return this.size === "LL" ? 2 : 1;
  }

  static getDefaultMachineType() {
    return MachineType.getMachineTypes()[0];
  }

  // /**
  //  * 
  //  * @static
  //  * @returns
  //  * @memberof MachineType
  //  */
  // static getMachineTypeNamesForDialog(){
  //   return MachineType.getMachineTypes().
  //           filter(machine => !machine.hasDoubleSeat).
  //           map(machine => machine.name);
  // }

  get name() {
    let name = MachineType.getNameOf(this);
    if (this.hasDoubleSeat) {
      name += "()";
    }
    return name;
  }

  //
  toWeightAndSize() {
    return _.pick(this, "weight", "size");
  }

  //
  static getWeightAndSize() {
    alert;
    return MachineType.getMachineTypes()
      .filter(machine => !machine.hasDoubleSeat)
      .map(machine => machine.toWeightAndSize());
  }

  static getNameOf(weightAndSize) {
    return weightAndSize.weight + "" + weightAndSize.size;
  }

  //
  static getMachineTypes() {
    let ret = [];

    ret.push(new MachineType("", "SS", false, 7, 10, 1, 1, 1, 2, 2, 1, 1));
    ret.push(new MachineType("", "S", false, 7, 9, 2, 1, 2, 2, 2, 1, 1));
    ret.push(new MachineType("", "M", false, 6, 8, 3, 1, 2, 2, 2, 2, 2));
    ret.push(new MachineType("", "L", false, 5, 7, 6, 1, 2, 3, 3, 2, 2));
    ret.push(new MachineType("", "LL", false, 4, 7, 10, 2, 2, 3, 3, 2, 2));

    ret.push(new MachineType("", "SS", false, 6, 8, 4, 1, 1, 2, 2, 1, 1));
    ret.push(new MachineType("", "S", false, 6, 7, 5, 1, 2, 2, 2, 1, 1));
    ret.push(new MachineType("", "M", false, 5, 6, 7, 1, 2, 2, 2, 2, 2));
    ret.push(new MachineType("", "L", false, 4, 5, 11, 1, 2, 3, 3, 2, 2));
    ret.push(new MachineType("", "LL", false, 3, 5, 13, 2, 2, 3, 3, 2, 2));

    ret.push(new MachineType("", "SS", false, 5, 6, 8, 1, 1, 2, 2, 1, 1));
    ret.push(new MachineType("", "S", false, 5, 5, 9, 1, 2, 2, 2, 1, 1));
    ret.push(new MachineType("", "M", false, 4, 4, 12, 1, 2, 2, 2, 2, 2));
    ret.push(new MachineType("", "L", false, 3, 3, 14, 1, 2, 3, 3, 2, 2));
    ret.push(new MachineType("", "LL", false, 2, 3, 15, 2, 2, 3, 3, 2, 2));

    ret.push(new MachineType("", "SS", true, 7, 10, 1, 1, 1, 2, 2, 1, 1));
    ret.push(new MachineType("", "S", true, 7, 9, 2, 1, 2, 2, 2, 1, 1));
    ret.push(new MachineType("", "M", true, 6, 8, 3, 1, 2, 2, 2, 2, 2));
    ret.push(new MachineType("", "L", true, 5, 7, 6, 1, 2, 3, 3, 2, 2));
    ret.push(new MachineType("", "LL", true, 4, 7, 10, 2, 2, 3, 3, 2, 2));

    ret.push(new MachineType("", "SS", true, 6, 8, 4, 1, 1, 2, 2, 1, 1));
    ret.push(new MachineType("", "S", true, 6, 7, 5, 1, 2, 2, 2, 1, 1));
    ret.push(new MachineType("", "M", true, 5, 6, 7, 1, 2, 2, 2, 2, 2));
    ret.push(new MachineType("", "L", true, 4, 5, 11, 1, 2, 3, 3, 2, 2));
    ret.push(new MachineType("", "LL", true, 3, 5, 13, 2, 2, 3, 3, 2, 2));

    ret.push(new MachineType("", "SS", true, 5, 6, 8, 1, 1, 2, 2, 1, 1));
    ret.push(new MachineType("", "S", true, 5, 5, 9, 1, 2, 2, 2, 1, 1));
    ret.push(new MachineType("", "M", true, 4, 4, 12, 1, 2, 2, 2, 2, 2));
    ret.push(new MachineType("", "L", true, 3, 3, 14, 1, 2, 3, 3, 2, 2));
    ret.push(new MachineType("", "LL", true, 2, 3, 15, 2, 2, 3, 3, 2, 2));

    return ret;
  }
}



