  constructor(
    name,
    rank,
    type,
    range,
    minRange,
    maxRange,
    rangeType,
    damage,
    damageType,
    mountPosition,
    minLimit,
    equipSamePosition,
    effect,
    maxLimit,
    edition
  ) {
    // 
    this.name = name;
    //  S,A,B
    this.rank = rank;
    //   
    this.type = type;
    // //  1,34,, 
    this.range = range;
    //  
    this.minRange = minRange;
    this.maxRange = maxRange;
    // AB) 
    this.rangeType = rangeType;
    //  
    this.damage = damage;
    //  
    this.damageType = damageType;
    // 
    this.mountPosition = mountPosition;
    // 
    this.minLimit = minLimit;
    // 
    this.equipSamePosition = equipSamePosition;
    // 
    this.effect = effect;
    // 
    this.maxLimit = maxLimit;
    //  
    this.edition = edition;
  }

  get formatMinLimit() {
    if (this.equipSamePosition) {
      return this.minLimit;
    } else {
      //FIXME 
      if (this.minLimit === 1) {
        return "";
      }
      if (this.minLimit === 2) {
        return "";
      }
      if (this.minLimit === 3) {
        return "";
      }
      if (this.minLimit === 4) {
        return "";
      } else {
        return this.minLimit;
      }
    }
  }

  //
  //
  calcDefaultItemCount() {
    //FIXME minLimit
    //
    if (this.minLimit === undefined) {
      return "";
    }

    if (this.equipSamePosition === true) {
      return this.minLimit;
    } else {
      return 1;
    }
  }

  //
  canEquip(equipmentPosition) {
    //2-1.
    let machineEquipmentPositions = MountPosition.toMachineEquipmentPosition(
      this.mountPosition
    );
    if (machineEquipmentPositions.indexOf(equipmentPosition) < 0) {
      return false;
    }
    return true;
  }

  //Range
  calcDamage(range) {
    let types = this.type.split("");
    //
    if (types.length === 0) {
      return this.doCalcDamage(
        this.minRange,
        this.maxRange,
        this.rangeType,
        this.damage,
        range
      );
    } else {
      //
      let minRanges = this.minRange.split("");
      let maxRanges = this.maxRange.split("");
      let rangeTypes = this.rangeType.split("");
      let damages = this.damage.split("");

      let one = this.doCalcDamage(
        minRanges[0],
        maxRanges[0],
        rangeTypes[0],
        damages[0],
        range
      );
      let two = this.doCalcDamage(
        minRanges[1],
        maxRanges[1],
        rangeTypes[1],
        damages[1],
        range
      );

      return Math.max(one, two);
    }
  }

  doCalcDamage(minRange, maxRange, rangeType, damage, range) {
    if (minRange === 0 || maxRange === 0) {
      return 0;
    } else {
      if (minRange <= range && range <= maxRange) {
        return damage;
      } else {
        if (rangeType === Equipment.RANGE_NORMAL) {
          return 0;
        } else {
          let diffMinRange = Math.abs(minRange - range);
          let diffMaxRange = Math.abs(maxRange - range);
          let maxDiff = Math.min(diffMinRange, diffMaxRange);

          let retVal;
          if (rangeType === Equipment.RANGE_OPTIMISTATION_A) {
            retVal = damage - maxDiff;
          } else {
            retVal = damage - maxDiff * 2;
          }
          return retVal > 0 ? retVal : 0;
        }
      }
    }
  }

  /**
   * 
   *
   * @param {*} type
   * @returns
   * @memberof Equipment
   */
  isTypeOf(type) {
    //1.
    if (this.type === type) {
      return true;
    } else {
      //
      let types = this.type.split("");
      return types.find(t => t === type) !== undefined;
    }
  }

  //
  static getEquipments() {
    return Equipment.assigns(Equipment.json());
  }

  /**
   * 
   *
   * @static
   * @param {} equipmentName
   * @returns 
   * @memberof Equipment
   */
  static findByName(equipmentName) {
    let filtered = Equipment.getEquipments().filter(equipment => {
      return equipment.name === equipmentName;
    });
    return filtered.length > 0 ? filtered[0] : null;
  }

  static assigns(array) {
    var retVal = [];
    array.forEach(obj => {
      var equipment = new Equipment();
      Object.assign(equipment, obj);
      retVal.push(equipment);
    });
    return retVal;
  }

